.training {
 position: relative;
 margin-bottom: 56px;
}

.training h3{
  font-size: 21px;
  font-weight: 700;
  line-height: 21px;
  margin: 0;
  margin-bottom: 54px;
}

.training h2{
  font-size: 35px;
  font-weight: 800;
  line-height: 100%;
  margin: 0;
  margin-bottom: 37px;
  position: relative;

  &::after{
    content: "";
    position: absolute;
    font-size: 55px;
    line-height: 55px;
    left: 0;
    bottom: -3px;
    opacity: 0.03;

    @include vp-1199 {
      font-size: 40px;
      line-height: 40px
    }
  }

  @include vp-1199 {
    font-size: 25px;
  }
}

.training p {
  margin: 0;
  max-width: 820px;
  margin-bottom: 56px;

  @include vp-1199 {
    max-width: 560px;
    margin-bottom: 25px;
  }
}
